.green-theme .page .page-head .page-title {
  color: #27ae60;
}

.blue-theme .page .page-head .page-title {
  color: #2980b9;
}

.red-theme .page .page-head .page-title {
  color: #c0392b;
}

.orange-theme .page .page-head .page-title {
  color: #d35400;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Open Sans', sans-serif;
}

.page {
  min-height: 100vh;
}

.page .content-area {
  width: calc(100% - 250px);
  min-height: 100vh;
  transition: .3s;
  float: right;
  will-change: width;
  background-color: #fbfbfb;
}

.page .content-area.no-sidebar {
  width: 100%;
}

.page .page-head {
  margin-top: 20px;
  padding: 20px;
}

.page .page-head .page-title {
  font-weight: bold;
  color: #27ae60;
}

header {
  background-color: #FFF;
  padding: 10px 0;
}

header .icons-contaienr {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header .icons-contaienr button:focus {
  box-shadow: none;
}

header .toggle-sidebar {
  cursor: pointer;
}

header .notifications-container .dropdown-menu {
  min-width: 600px;
  border: none;
  padding: 0;
  top: 10px !important;
}

header .notifications-container .dropdown-menu:before {
  content: "";
  position: absolute;
  top: -19px;
  left: 0;
  border-style: solid;
  border-width: 10px;
  border-color: transparent transparent #333 transparent;
}

header .notifications-container .noti-head {
  background-color: #333;
  color: #FFF;
  padding: 15px;
  font-weight: bold;
}

header .notifications-container .noti-head span {
  float: right;
}

header .notifications-container .noti-content {
  padding: 15px;
}

header .notifications-container .noti-content ul {
  margin: 0;
}

header .notifications-container .noti-content ul li {
  padding: 5px;
  font-weight: bold;
  color: #777;
  border-bottom: 1px solid #e9e8e8;
  margin-bottom: 10px;
  font-size: 15px;
}

header .notifications-container .noti-content ul li svg {
  width: 20px;
  margin-right: 10px;
}

header .notifications-container .noti-content ul li:first-child {
  padding-top: 0;
}

header .notifications-container .noti-content ul li:last-child {
  padding-bottom: 0;
  margin-bottom: 0;
  border-bottom: none;
}

header .notifications-container .noti-foot a {
  display: block;
  padding: 10px;
  border-top: 1px solid #EEE;
}

header .notifications-container .noti-foot a:hover {
  text-decoration: none;
}

header .usermenu-container .username {
  cursor: pointer;
}

header .usermenu-container .username .avatar {
  width: 40px;
  height: 40px;
}

header .usermenu-container .username .user {
  font-weight: bold;
  color: #777;
  margin-right: 10px;
  margin-left: 10px;
}

header .usermenu-container .dropdown-menu {
  min-width: 517px;
  border: 1px solid #DDD;
  padding: 15px;
  top: 10px !important;
}

header .usermenu-container .dropdown-menu:before {
  content: "";
  position: absolute;
  top: -19px;
  right: 20px;
  border-style: solid;
  border-width: 10px;
  border-color: transparent transparent #DDD transparent;
}

header .usermenu-container .dropdown-menu .user-info h4 {
  font-weight: bold;
}

header .usermenu-container .dropdown-menu .user-info p {
  color: #888;
}

header .usermenu-container .dropdown-menu .activities-list li {
  padding: 10px;
}

header .usermenu-container .dropdown-menu .usermenu-links .link-box {
  background-color: #f3f3f3;
  padding: 10px 15px;
  border-radius: 4px;
  text-align: right;
}

header .usermenu-container .dropdown-menu .usermenu-links .link-box svg {
  float: left;
  position: relative;
  top: 5px;
}

header .usermenu-container .dropdown-menu .usermenu-links .link-box svg.fa-cog {
  color: #2980b9;
}

header .usermenu-container .dropdown-menu .usermenu-links .link-box svg.fa-user {
  color: #16a085;
}

header .usermenu-container .dropdown-menu .usermenu-links .link-box svg.fa-sign-out-alt {
  color: #c0392b;
}

.page .sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #232323;
  color: #FFF;
  padding: 15px;
  transition: .3s;
}

.page .sidebar.no-sidebar {
  left: -250px;
}

.page .sidebar .logo-area {
  font-weight: bold;
  font-size: 1.5rem;
  margin-bottom: 15px;
}

.page .sidebar .links-area li a {
  color: #acacac;
  padding: 10px 20px;
  display: block;
  transition: .3s;
}

.page .sidebar .links-area li a:hover {
  text-decoration: none;
  color: #FFF;
}

.page .sidebar .links-area > li > a {
  color: #FFF;
  position: relative;
}

.page .sidebar .links-area > li > a::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid #FFF;
  transform: translateY(-50%);
}

.page .sidebar .links-area > li > a:hover {
  padding-left: 25px;
}

.page .sidebar .links-area > li > a.active::before {
  border-color: #ffc107;
}

.page .sidebar .links-area > li > a .fa-angle-right {
  float: right;
  position: relative;
  top: 6px;
  transition: .3s;
}

.page .sidebar .links-area > li > a .fa-angle-right.down {
  transform: rotate(90deg);
}

.page .sidebar .links-area .child-links {
  position: relative;
  display: none;
}

.page .sidebar .links-area .child-links::before {
  content: '';
  position: absolute;
  top: 0;
  left: 3px;
  width: 3px;
  height: 100%;
  background-color: #666;
}

.page .sidebar .links-area .child-links a {
  position: relative;
}

.page .sidebar .links-area .child-links a:hover::before {
  content: '';
  position: absolute;
  top: 0;
  left: 3px;
  width: 3px;
  height: 100%;
  background-color: #FFF;
}

.settings-box {
  background-color: #232323;
  color: #FFF;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1000;
  width: 250px;
  height: 100%;
  padding: 15px;
  transition: .5s;
}

.settings-box .option-title {
  font-weight: bold;
  margin-bottom: 15px;
  color: #DDD;
  text-align: center;
  font-size: 18px;
}

.settings-box .toggle-settings {
  background-color: #232323;
  color: #FFF;
  position: absolute;
  top: 100px;
  left: -40px;
  padding: 8px 8px;
  width: 40px;
  height: 40px;
  border-radius: 10px 0 0 10px;
  cursor: pointer;
}

.settings-box.hide-settings {
  right: -250px;
}

.settings-box .color-options {
  margin-bottom: 15px;
  border-bottom: 1px solid #EEE;
}

.settings-box .color-options li:nth-child(1) {
  background-color: #27ae60;
}

.settings-box .color-options li:nth-child(2) {
  background-color: #2980b9;
}

.settings-box .color-options li:nth-child(3) {
  background-color: #c0392b;
}

.settings-box .color-options li:nth-child(4) {
  background-color: #d35400;
}

.settings-box .color-options ul {
  display: flex;
  justify-content: space-evenly;
}

.settings-box .color-options ul li {
  width: 22px;
  height: 22px;
  cursor: pointer;
  border-radius: 50%;
  opacity: .6;
  transition: .5s;
}

.settings-box .color-options ul li.active, .settings-box .color-options ul li:hover {
  opacity: 1;
}
