$themes: (
  green-theme: (
    color: #27ae60
  ),
  blue-theme: (
    color: #2980b9
  ),
  red-theme: (
    color: #c0392b
  ),
  orange-theme: (
    color: #d35400
  )
);

@each $theme, $map in $themes {
  .#{$theme} {
    $color: map-get($map, color);
    .page {
      .page-head {
        .page-title {
          color: $color;
        }
      }
    }
  }
}